.hero {
  background-image: url("../../../../public/images/2.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 90vh;
  width: 100%;
}
.hero .container {
  padding-top: 15%;
}
.hero h1 {
  color: orange;
  font-size: 60px;
}
.hero p {
  color: black;
  font-size: 20px;
  font-weight: bold;
  opacity: 0.8;
}

@media screen and (max-width: 800px) {
  .hero .container {
    padding-top: 30%;
  }
}
