.conatct .shadow
{
  background: lightyellow;
}
.contact form div {
  display: flex;
}

.contact label{
  color: black;
  font-size: 18px;
  
}
.contact textarea {
  border: 1px solid grey;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 20px;
}
.contact input {
  margin-bottom: 20px;
  margin-right: 10px;
  border: 1px solid grey;
}
.contact form {
  padding: 30px;
}
@media screen and (max-width: 768px) {
  .contact h4 {
    width: 100%;
    margin-bottom: 30px;
  }
  .contact form div {
    flex-direction: column;
  }
  .contact input {
    width: 100%;
  }
}
